        </p-scroll-panel>

        <p-button
            label="Save"
            class="p-mt-2 p-mr-2"
            @click="save()"
            v-if="hasUpdatePermission"
        />
        <p-button
            label="Create"
            class="p-mt-2 p-mr-2"
            @click="createFetilizationRow()"
            v-if="hasCreatePermission"
        />
        <p-button label="Report" class="p-mt-2" @click="openPdfReport()" />

        <fertilization-line-create-edit-dailog />
